@import "styles/color.scss";

.root {
  * {
    user-select: none;
  }
  .badge {
    padding: 4px 7px;
    border-radius: 4px;
    font-weight: bold;
    color: white;
  }
  .active {
    .table-cell:first-child {
      border-left: solid transparent;
    }
    .badge {
      background-color: $color-3 !important;
    }
  }
  .due {
    .table-cell:first-child {
      border-left: solid $color-7;
    }
    .table-cell {
      background-color: $color-26 !important;
    }
    .badge {
      background-color: $color-7 !important;
    }
  }
  .warning {
    .table-cell:first-child {
      border-left: solid transparent;
    }
    .badge {
      background-color: $color-8 !important;
    }
  }

  .table-row:hover > td {
    background-color: rgba(0,0,0,.05) !important;
  }

  .overflow {
    text-align: left;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-left: 6px;
  }
  .cellresponsive {
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
  }
}

