.export-span {
    margin-right: 10%;
    position: absolute;
    margin-top: 1.4%;
    border-radius: 10px;
    border-style: outset;
    background-color: #F4F6F8;
    width: 200px;
    height: 140px;
    .header {
        display: flex;
        z-index: 1;
        width: 100%;
        height: 10px;
        .title {
            justify-content: flex-start;
            font-weight: bolder;
            margin-top: -4%;
        }
        .close-button {
            margin-top: -25px;
            margin-left: auto;
            justify-content: flex-end;
        }
    }
    .close {
      display: flex;
      align-items: center;
      margin-right: 20%;
      .close-container {
        margin-top: 5%;
        margin-left: auto;
        margin-right: 6%;
        justify-content: flex-end;
        align-items: center;
        .close-button {
          align-items: center;
          color: white;
          background-color: #3ab77d;
          width: 90px;
          text-transform: none;
          &:hover {
            background-color: #0b4930;
          }
        }
      }

    }
    .paper {
      margin-top: 5%;
      margin-left: 5%;
      display: flex;
      width: 355px;
    }
    .checkbox {
      margin-left: 10px;
      margin-top: 20px;
      .checkbox-container {
          display: flex;
          margin-top: 3%;
        .xls {
            justify-content: flex-start;
            font-weight: bold;
            display: flex;
            .xls-label {
              margin-top: 10px;
            }
          }
          .pdf {
            justify-content: flex-end;
            display: flex;
            font-weight: bold;
            display: absolute;
            .pdf-label {
              margin-top: 10px;
            }
          }
      }
    }
  }