.root {
    width: 500px;
    flex-shrink: 0;
    height: 900px;
    position: absolute;
    overflow: hidden;
    .drawer-paper {
      width: 500px;
      position: absolute;
      z-index: 1;
      overflow: hidden;
    }
    .drawer-container {
      height: 900px;
      position: absolute;
    }
    .drawer-root {
        border-style: none;
        position: absolute;
        .drawer-header {
            height: 70px;
            display: flex;
            width: 500px;
            border-style: outset;
            align-items: center;
            position: absolute;
            background-color: #C1C1C1;
            z-index: 1;
            .buttons {
                margin-left: auto;
                margin-right: auto;
                .thumbnail {
                  justify-content: center;
              }
            }
          }
        .drawer-body {
            border-style: outset;
            position: absolute;
            width: 500px;
            height: 900px;
            overflow-y: auto;
            overflow-x: hidden;
            background-color: #F4F6F8;
            .thumbnail-container {
              align-items: center;
              justify-content: center;
              top: 20%;
              margin-top: 20% !important;
              margin:0 auto;
              width: 430px;
              height: 200px;
              .link {
                &:hover {
                  background: gold;
                }
              }
            }
            .bookmark-container {
              align-items: center;
              justify-content: center;
              top: 20%;
              margin-top: 20% !important;
              margin-left: 2%;
              width: 430px;
              height: 200px;
              .link {
                &:hover {
                  background: gold;
                }
              }
            }
        }
    }

  }