.root {
  overflow-x: unset !important;
  position: relative;

  .loader {
    position: absolute;
  }

  table {
    min-width: 500px;
  }

  .theadWithHeight {
    display: table;
    width: 100%;
    table-layout: fixed;
  }

  .tbodyWithHeight {
    display: block;
    table-layout: fixed;
    overflow-y: auto;

    & > tr {
      display: table;
      width: 100%;
      table-layout: fixed;
    }
    
  }

}

.footer {
  border-bottom: 0.5px solid #cacaca;
  border: unset !important;
  display: flex !important;
  justify-content: flex-end;
  padding-top: 4px;

  td {
    border: unset !important;
  }
}
