@import "styles/color.scss";

.root {
  .title {
    &.center {
      text-align: center;
    }
    .text-container {
      height: 100%;
      .label {
        user-select: none;
      }
    }
  }

  .text-padding {
    padding-left: 15px;
  }

  .vertical-container {
    height: 100%;
  }

  .lead {
    font-size: 14px;
    text-align: left;
    font-weight: 600;
  }

  .form {
    font-size: 14px;
    font-weight: 600;
  }

  .error-wrapper {
    .error {
      padding-top: 1px;
      color: $color-17;
    }
  }
}

.date-picker-wrapper {
  border: solid 1px rgba(0,0,0,.2);
  border-radius: 5px;
  padding: 1px 5px;

  .input {
    input {
      cursor: pointer;
      font-size: 14px;
      text-overflow: ellipsis;
    }
    &.active { color: rgba(0,0,0,0.9); }
    &.readonly { input { cursor: unset !important; } }
  }
}
