.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      // display: flex;
      align-items: center;
      padding: 10px;

      .button {
        float: right !important;
        // flex-direction: row;
        // flex-wrap: wrap;
      }

      .title {
        // flex-direction: row;
        // flex-wrap: wrap;
        font-size: 20px;
        font-weight: 500;
        padding-top: 20px;
        padding-left: 10px;
      }

      .subtitle {
        // flex-direction: row;
        // flex-wrap: wrap;
        font-weight: bold;
        padding-top: 10px;
        padding-left: 8px;
      }
    }
  }

  .dialog-content {
    font-size: 16px;
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin-bottom: 10px;
    }
  }

  .leftGrid {
    width: 50%;
    padding-left: 10px;
    padding-right: 1.5rem;
    padding-bottom: 10px;
  }

  .rightGridSuccess {
    width: 50%;
    padding-right: 20px;
    padding-left: 1.5rem;
    padding-bottom: 35px;
  }

  .rightGrid {
    width: 50%;
    padding-right: 10px;
    padding-left: 1.5rem;
    padding-bottom: 10px;
  }

  .button-positive {
    padding: .75rem 1rem;
    color: #fff;
    background: #3ab77d;
    width: 100%;
    color: white;
    // background-color: #3ab77d;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    padding: .75rem 1rem;
    border: 1px solid #3ab77d;
    width: 100%;
    color: #3ab77d;
    // border-color: #3ab77d;

    &:hover {
      background-color: #3ab77d;
      border-color: white;
      color: white;
    }
  }

  .alignEnd {
    text-align: end;
  }
}
