@import "styles/color.scss";

.root {
  .header {
    background-color: $color-22;
    padding: 20px 10px;
    & h5 {
      font-weight: 500;
      color: rgba(0, 0, 0, 0.8);
      letter-spacing: 0.1px;
    }
  }
  .form {
    padding: 30px;
  }
  .viewer {
    min-height: 400px;
    background: #fff;
  }
  .generate {
    background: $color-13;
    color: white;
    width: 100%;
    padding: 10px;
  }
  .action {
    position: relative;
    .control {
      padding: 10px;
      color: $color-13;
      cursor: pointer;
      .icon {
        margin-right: 5px;
      }
      .download-icon {
        color: #fff;
        background: $color-13;
        border-radius: 50%;
      }
      .label {
        text-decoration: underline;
      }
    }
    .dialog {
      position: absolute;
      z-index: 2;
      background: #fff;
      top: 100%;
      border-radius: 3px;
      padding: 10px;
      display: none;
      text-align: center;
      width: 200px;
      &:hover {
        display: block;
      }
      .download {
        padding: 10px 10px 5px 10px;
        background: $color-13;
        color: #fff;
        .download-text {
          font-size: 13px !important;
        }
        .icon-wrapper {
          line-height: 0;
        }
      }
    }
    &:hover {
      .dialog {
        display: block;
      }
    }
  }
}
