@import "styles/color.scss";

.root {
  height: 450px;
  border-radius: 10px !important;

  .btnSelect {
    width: 100%;
    text-align: center;
    border: 1px solid #dadada;
    border-radius: 5px;
    font-size: 12px;
  }

  .wrapper {
    padding: 22px;
    height: 100%;
    display: flex;
    flex-direction: column;

    .header {
      display: flex;
      flex-direction: column;

      .input {
        padding-left: 10px;
      }

      .first-row {
        display: flex;
        flex: 1;

        .count {
          h5 {
            font-weight: bold;
          }
        }

        .filter {
          flex: 1;
          display: flex;
          justify-content: flex-end;
        }
      }

      .second-row {
        display: flex;
        flex: 1;
        align-items: center;

        .title {
          color: $color-12;
          font-weight: bold;
          flex: 1;
        }

        .filter {
          flex: 1;
          display: flex;
          justify-content: flex-end;
        }
      }
    }

    .list-wrapper {
      flex: 1;
      overflow: hidden;
      .list {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
  
    //Ann here
        // /* width */
        // &::-webkit-scrollbar {
        //   width: 6px;
        // }
  
        // /* Track */
        // &::-webkit-scrollbar-track {
        //   /* box-shadow: inset 0 0 5px grey; */
        //   background: rgba(0,0,0,.09);
        //   border-radius: 6px;
        // }
        
        // /* Handle */
        // &::-webkit-scrollbar-thumb {
        //   background: rgba(0,0,0,.15); 
        //   border-radius: 6px;
        // }
  
        // /* Handle on hover */
        // &::-webkit-scrollbar-thumb:hover {
        //   background: rgba(0,0,0,.6); 
        // }
  
        table {
          & > thead {
            background: white;
  
            & > tr > th {
              background: white !important;
            }
          }
  
          & > tbody {
            & tr > td {
              border: 0;
            }
          }
        }
      }
    }

    .action {
      display: flex;
      justify-content: flex-end;
      padding-top: 15px;
      font-weight: bold;
      color: $color-12;
    }
  }
}
