.root {
  display: flex;
  position: relative;
  background: #525659;
  overflow: scroll;
  .drawer {
    margin-top: 70px;
    width: 500px;
    flex-shrink: 0;
    height: 100%;
    position: absolute;
  }
  .toolbar{
    display: flex;
    padding: 10px;
    position: absolute;
    min-height: 60px;
    height: 70px;
    width: 100%;
    z-index: 1;
    background-color: #323639;
  }
  .controls {
    display: flex;
    width: 100%;
    .left-pane {
      display: flex;
      align-items: center;
      margin-left: -50px;
      justify-content: start;
      .control-left {
        color: white;
        font-size: 17px;
      }
      .search-span {
        margin-left: 45px;
        position: absolute;
        top: 30%;
        border-style: outset;
        background-color: #F4F6F8;
        width: 400px;
        height: 250px;
        .close {
          display: flex;
          align-items: center;
          .close-container {
            margin-top: 5%;
            margin-left: auto;
            margin-right: 6%;
            justify-content: flex-end;
            align-items: center;
            .close-button {
              align-items: center;
              color: white;
              background-color: #3ab77d;
              width: 90px;
              text-transform: none;
              &:hover {
                background-color: #0b4930;
              }
            }
          }

        }
        .paper {
          margin-top: 5%;
          margin-left: 5%;
          display: flex;
          width: 355px;
        }
        .checkbox {
          margin-left: 10px;
          margin-top: 20px;
          .match-case {
            font-weight: bold;
            display: flex;
            .match-case-label {
              margin-top: 10px;
            }
          }
          .match-whole-word {
            display: flex;
            font-weight: bold;
            display: absolute;
            .match-whole-word-label {
              margin-top: 10px;
            }
          }
        }
      }
      .input {
        align-items: center;
        justify-content: start;
        font-weight: bold;
        height: 30px;
        width: 50px;
      }
      .number-pages {
        margin-left: 10px;
        color: white;
      }
    }
    .middle {
      margin-left: auto;
      margin-right: auto;
      align-items: center;
      justify-content: center;
      .zoom {
        margin-left: auto;
        color: white;
        font-size: 17px;
      }
      .zoomout {
        margin-left: -10px;
        margin-right: auto;
        color: white;
        font-size: 17px;
      }
      .autozoom {
        margin-left: auto;
        margin-right: auto;
        background: 4f4f4f;
        width: auto;
        color: white;
        font-size: 16px;
      }
    }
    .far-right{
      justify-content: end;
    }
    .item-right-side {
      border-radius: 3pt;
      margin-left: auto;
    }
    .control {
      margin-left: -8px;
      color: white;
      font-size: 17px;
    }
    .page {
      margin: 12px 0px 0px 20px;
      color: gray;
      float: left;
    }

    .bar {
      margin: 0px 0px -8px 2px;
      padding: 1px;
      color: white;
      font-size: 17px;
    }

    .back {
      margin-left: -13px;
      color: white;
      font-size: 17px;
    }

    .fullscreen {
      margin-left: 100px;
      // left: 30%;
      color: white;
      font-size: 12px;
    }
    .to-print-bir {
      // left: 30%;
      color: white;
      font-size: 12px;
    }
    .print {
      // left: 30%;
      color: white;
      font-size: 12px;
    }
    .thumbnail {
      // left: 30%;
      color: white;
      font-size: 12px;
    }
    .download {
      // left: 30%;
      color: white;
      font-size: 12px;
    }
    .bookmark {
      // left: 30%;
      color: white;
      font-size: 12px;
    }
  }

  .pdf {
    display: flex;
    flex: 1;
    justify-content: center;
    padding: 10px;
    padding-top: 70px;
    .outline{
      display: none;
    }
  }

  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }
}

.pdfDocument {
  visibility: hidden;
}
