@import "styles/color.scss";

.root {
  display: grid;
  .header {
    indicator {
      background-color: green;
    }

    .item {
      margin-right: 15px;
      margin-left: 0px;
      font-weight: 400;
      // color: $color-11;
      cursor: pointer;
      &:hover,
      &.active {
        color:  #4cb87d; //#3ab77d;
        text-decoration: none;
      }

    }
  }
  .divider {
    background: #0d0d0e;
    height: 2px;
  
  }
  .panels {
    @media only screen and (max-width: 1300px) {
      width: 100%;
    }
    @media only screen and (max-width: 1280px) {
      width: 90%;
    }
  }
}
